<template>
	<div>
		<div class="shop-goods">
			<ul>
				<li v-for="(item,index) in goodsCart" :key="index">
					<mt-switch  v-model="item.isSelected"></mt-switch>
					<div class="img">
						<img :src="item.goodimg" alt="">
					</div>
					<div class="title">{{item.title}}</div>
					<div class="price-num clearfix">
						<div class="price left"><em>价格:</em>{{item.price}}</div>
						<div class="right num-box">
									<span class="reduce-num btn" @click="reduceNum(item.goodId)">-</span><span class="num">{{item.num}}</span><span class="add-num btn" @click="addNum(item.goodId)">+</span>
						</div>
					</div>
					<div class="delete">
						<mt-button  type="danger" @click="delGoodsCart(index)">删除</mt-button>
					</div>
				</li>
			</ul>
		</div>
		<div class="result  clearfix">
			<div class="left">
				<em>全选:</em><mt-switch  v-model="value" @click="isShow"></mt-switch>
			</div>
			<div class="right">
				<div class="total"><em>合计:</em><span>{{priceTotal}}</span></div>
				<mt-button  type="danger">结算</mt-button>
			</div>
		</div>
	</div>
</template>
<script>
import {mapState,mapGetters,mapActions} from "vuex";
	export default {
		data(){
			return{
				value:false,
			}
		},
		mounted(){
			this.value = this.isSwitch;
		
		},
		computed:{
			...mapState({
				goodsCart : state=>state.shopcart.goodsCart
			}),
			...mapGetters({
				priceTotal : 'priceTotal',
				isSwitch:'isSwitch'
			})
		},
		watch:{
			isSwitch(newV){
				this.value = this.isSwitch;
			},
			value(newV){
				if(newV){
					this.goodsCart.forEach(el => {
						el.isSelected = true;
					})
				}else{
					this.goodsCart.forEach((el,index) => {
						el.isSelected = false;
					})
				}
			}
		},
		methods:{
			...mapActions([
				'delGoodsCart',
				
			]),
			isShow(){
				console.log(111);
			},
			reduceNum(id){
				for(var i = 0 ; i<= this.goodsCart.length-1; i++){
					if(this.goodsCart[i].goodId == id){
						if(this.goodsCart[i].num <= 0){
							this.$toast("亲，你不可以给我哦")
							return;
						}
						this.goodsCart[i].num --;
					}
				}
			},
			addNum(id){
				for(var i = 0 ; i<= this.goodsCart.length-1; i++){
					if(this.goodsCart[i].goodId == id){
						if(this.goodsCart[i].num >= this.goodsCart[i].stock){
							this.$toast("亲，没有那多的库存了哦")
							return;
						}
						this.goodsCart[i].num ++;
					}
				}
			},

		},
		beforeRouteLeave(to,from,next){
			if(from.name == "shopcart"){
				window.localStorage.setItem("goodList",JSON.stringify(this.goodsCart));
			}
			next();
		}
	}
</script>
<style lang="less" scoped>
	.box{
		width:200px;
		height:200px;
		background: red;
	}
	.shop-goods{
		ul{	

			padding: 0 12px 41px; 
			li{
				position: relative;
				padding: 15px 0 15px 150px;
				height: 150px;
				border-bottom: 1px solid #ccc;
				overflow:hidden;
				.img{
					height: 90px;
					width: 75px;
					position: absolute;
					top: 30px;
					left: 60px;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.title{
					font-size: 14px;
					color: #a7a7a7;
					height: 57px;
					overflow:hidden;
				}
				.mint-switch{
					position:absolute;
					top: 60px;
					left: 0;
				}
				.price-num{
					margin-top: 4px;
					.left{
						padding-top: 4px;
					}
					.price{
						font-size: 14px;
						color: red;
						em{
							color: #c7c7c7;
							margin-right: 5px;
						}
					}
					.btn{
						display: inline-block;
						width: 25px;
						height: 25px;
						text-align: center;
						line-height: 23px;
						border: 1px solid #ccc;
					}
					.add-num{
						border-left: none;
						border-top-right-radius: 5px; 
						border-bottom-right-radius: 5px; 
					}
					.reduce-num{
						border-right: none;
						border-top-left-radius: 5px; 
						border-bottom-left-radius: 5px; 
					}
					.num{
						display: inline-block;
						width: 25px;
						height: 25px;
						text-align: center;
						line-height: 23px;
						border: 1px solid #ccc;
						position:relative
					}
					.mint-button {
						margin: 10px 0;
						margin-right:20px;
					}
				}
				.delete{
					.mint-button{
						height: 25px;
						width: 100%;
						font-size: 12px;
						margin-top: 12px;
					}
				}
			}
		}
		
	}
	.result{
		position:fixed;
		left: 0;
		bottom: 59px;
		width: 100%;
		padding: 4px 10px 0; 
		border-top: 1px solid #ccc;
		background: #fff;
		.left{
			font-size: 14px;
			.mint-switch{
				display: inline-block;
				vertical-align: middle;
				margin-left:  10px;
			}
		}
		.right{
			.total{
				display: inline-block;
				margin-right: 10px;
				span{
					color:red;
				}
			}
			.mint-button{
				min-width: 75px;
				height: 30px;
				font-size: 14px;
				padding: 0 8px;
			}

		}
	
		
	}
</style>


